@import url(https://fonts.googleapis.com/css2?family=Dosis&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;500&family=Source+Code+Pro:wght@500&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.container-head {
  text-align: center;
  height: 100vh;
}
.name-styling {
  margin-top: 20%;
  letter-spacing: 0.6em;
  word-break: break-all;
  color: aliceblue;
  word-break: break-all;
  font-size: 5vw;
  font-family: Dosis ,sans-serif;
  font-weight: 200;
}

.name-styling-subheading {
  letter-spacing: 0.3em;
  margin-top: 1em;
  font-size: 2.3vw;
  word-break: break-all;
  color: aliceblue;
  font-family: Dosis ,sans-serif;
}
.jumbo {
  background-color: aqua;
  background: url(/static/media/BLack.b98b6475.jpg) no-repeat center center;
  width: 100%;
  height: auto;
  /* -webkit-background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -o-background-size: 100% 100%; */
  /* background-size: 100% 100%;  */
}
.what-i-do {
  font-size: 10vw;
  font-family: Dosis ,sans-serif;
  color: rgb(222, 227, 231);
  margin: 1%;
}

.what-i-do-text {
  font-size: 5.2vw;
  font-family: Dosis ,sans-serif;
  text-align: center;
  color: rgb(222, 227, 231);
}

.navbar {
    opacity: 0.9;
    background-color: black;
    font-family: Dosis ,sans-serif;
}

.marquee {
    font-weight: 200;
    font-size: 55px;
    font-style: oblique;
    color: black;
    -webkit-text-fill-color: #1e2127; /* Will override color (regardless of order) */
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
  }
  

.codeLineNumber {
    font-size: 12px;
    font-family: Source Code Pro, monospace;
    width: 0.1%;
    color: #ffffff;
  }

  @media (min-width: 768px) {
  .what-i-do-text {
    font-size: 40px;
  }
}
/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {
  .what-i-do-text {
    font-size: 40px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .what-i-do-text {
    font-size: 48px;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 2560px) {
  .what-i-do-text {
    font-size: 80px;
  }
}


